@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url(https://fonts.googleapis.com/css?family=Abel);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    font-family: 'Nunito', sans-serif;
    color: #537c8e;
}

body {
    background-color: #f2f0ec;
}

h1 {
    text-transform: uppercase;
    font-weight: 800;
    font-size: 4rem;
    text-shadow: 1px 1px white;
}

p {
    color: #eff1e4;
    font-size: 1.2rem;
}

.mybtn {
    margin-left: 5px;
    margin-top: 5px;
    background: #4caf50;
    color: #eff1e4;
    box-shadow: 1px 1px white;
}
.backgroundBtn{
    background: #f83d02;
    text-align: right;
}

.mybtn:hover {
    background: #eff1e4;
    color: #537c8e;
    box-shadow: 1px 1px #537c8e;
    text-decoration: none;
}

@media only screen and (max-width: 770px) {
    h1 {
        font-size: 2.5rem;
    }
    p {
        font-size: 1rem;
    }
}
.navbar-custom {
    background-color: #6727b09e !important;
    left: 0;
    top: 0;
    height: 10vh;
    z-index: 1;
    border-bottom: 4px solid #2ebdbd;
    width:100%;
}
@media only screen and (max-width: 770px) {
    .navbar-custom {
      height:100%;
    }
  }
.navbar-custom .navbar-brand,
.navbar-custom .navbar-text {
    color: #eff1e4;
    text-transform: uppercase;
    font-family: 'Abel', sans-serif;

}
.navbar-custom .navbar-toggler {
    border:none;
}
/* Create a "push" feeling to the burger */
.navbar-custom .navbar-toggler:active {
    font-size: 1rem;
}
.navbar-custom .navbar-toggler-icon{
    background-image: url(
        "data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='#2ebdbd' stroke-width='3' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}
/* change the link color */
.navbar-custom .navbar-nav .nav-link {
    color: #eff1e4;
}
.navbar-custom .navbar-nav .nav-link:hover{
    color:#ceae7f;
}

.navbar-light .navbar-nav .nav-link.active{
    color: #ceae7f;
}

.breed-list {
    padding-top: 10px;
    padding-bottom: 10px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
}


.form-control {
    background: #eff1e4;
    margin: 0 auto;
    border-radius: 200px;
    width: 300px;
    padding: 10px 10px;
    text-align: center;
    color: #537c8e !important;
    border: none !important;
    box-shadow: 2px 2px#537c8e;
}

.form-control:focus {
    background: #eff1e4;
    text-align: center;
    color: #537c8e !important;
    border: none !important;
}


#breed-item-btn {
    padding: 10px;
    border-radius: 10px;
    text-align: center;
}

.breed-counter {
    text-transform: uppercase;
    padding-top: 10px;
    font-size: 1.8rem;
    font-weight: 800;
    color: #537c8e;
    text-align: center;
    text-shadow: 0.5px 0.5px #eff1e4;
}

@media only screen and (max-width: 770px) {
    .breed-list {
        grid-template-columns: repeat(1, 1fr);
    }
    .breed-counter{
        font-size:1.2rem;
    }
}
.stars {
    display: inline-flex;
    position: relative !important;
}

.card {
    background: #eff1e4;
    border: none;
}

.card-body {
    border-top: red !important;
}

.single-breed-header {
    text-align: left;
    text-transform: uppercase;
    font-weight: 800;
    font-size: 2rem;
}
.cat-stats {
    text-align: left;
    font-size: 20px;
}

.origin {
    text-transform: uppercase;
    font-size: 18px;
    padding: 10px 20px;
    margin-right: 10px;
    margin-top: 10px;
}

.cat-stars-grid {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: "a a b b"
}

.cat-stars-item {
    grid-area: a;
}

.cat-stars-item2 {
    grid-area: b;
}
@media only screen and (max-width: 770px) {
    .single-breed-header {
        text-align: center;
    }
    .badges {
        text-align: center;
    }
    .cat-stars-grid {
        grid-template-areas: 
        "a a a a" 
        "b b b b";
    }
}
.modal-content {
    background: transparent;
    width: auto;
}
.modal-header {
    border: none;
}

.modal-footer {
    border: none;
    padding: 0px;
}

.modal-body {
    padding: 0px;
}


.grid-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    grid-auto-rows: 300px;
    grid-template-areas: "a a" "a a";
}

.grid-item {
    text-align: center;
    margin-bottom: 20vh;
    grid-area: a;
    justify-self: center;
    align-self: center;
}
